.resume {
  height: 100%;
  display: flex;
  flex-direction: row;
  border: 1px solid blue;
  margin: 3px;
}
.profile {
  display: flex;
  flex-direction: row;
  flex: 1;
  padding: 10% 0 10% 0;
  border: 1px solid black;
  margin: 3px;
}
.skill {
  display: flex;
  flex-direction: row;
  flex: 1;
  padding: 10% 0 10% 0;
  border: 1px solid red;
  margin: 3px;
}
.profile_pic {
  width: 40%;
  border: 1px solid black;
  margin: 2px;
}
.line {
  width: 10%;
  margin: 2px;
}

.skill_text {
  flex: 1;
  border: 1px solid black;
  margin: 2px;
}
.skill_logo {
  flex: 1;
  padding: 10% 0 10% 0;
  margin: 2px;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
}
.skill_logo div {
  margin: 3%;
  width: 23%;
  height: 30%;
}
.java {
  background-image: url(../image/java.png);
  background-size: 100% 100%;
}
.html {
  background-image: url(../image/html.png);
  background-size: 100% 100%;
}
.css {
  background-image: url(../image/css.png);
  background-size: 100% 100%;
}
.spring {
  background-image: url(../image/spring.png);
  background-size: 100% 100%;
}
.springboot {
  background-image: url(../image/springboot.jpg);
  background-size: 100% 100%;
}
.mysql {
  background-image: url(../image/mysql.png);
  background-size: 100% 100%;
}
.line_line {
  background-color: #444a62;
  border: 1px solid #444a62;
  border-radius: 10px;
  width: 15%;
  height: 80%;
  margin: 35% auto;
}
.line_dot1 {
  background-color: #3c404e;
  border: 1px solid #3c404e;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: -170% auto;
}
.line_dot2 {
  background-color: #3c404e;
  border: 1px solid #3c404e;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: -200% auto;
}

.line_dot3 {
  background-color: #3c404e;
  border: 1px solid #3c404e;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: -270% auto;
}
.line_dot_text {
  margin-left: 200%;
  width: 350px;
}
