.nav {
  display: flex;
  height: 10vh;
  width: 40%;
  background-color: #444a62;
  margin-left: 50%;
  margin-right: 10%;
  border-radius: 5px 5px 35px 35px;
}

.nav a {
  width: 70px;
  padding: 10px;
  text-decoration: none;
  color: white;
  font-weight: 600;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: flex-end;
  font-size: 20px;
  margin-bottom: 20px;
}
