.portfolio {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid black;
  margin: 3px;
  padding: 2% 0 10% 0;
}
h1 {
  margin: 1rem;
  font-size: 300%;
}
.portfolio_list {
  display: flex;
  border: 1px solid red;
  margin: 0 auto;
}
.guestbook,
.meett,
.woo {
  display: flex;
  flex-direction: column;
  text-decoration-line: none;
  border: 1px solid black;
  width: 20vw;
  margin: 2.5vw;
  height: 50vh;
}
.image {
  border: 1px solid blue;
  width: 15vw;
  margin: 0 auto;
  height: 30vh;
}
.detail {
  border: 1px solid green;
  width: 15vw;
  margin: 1rem auto;
  height: 15vh;
}
